.list{
    width : 100%;
    max-height: 50vh;
    overflow-y:scroll;
    margin: 4px;
}
.todo-input-form{
    display: flex;
    flex-direction: column;
    padding: 4px;
    gap: 0.5rem;
    align-items: center;
  

}
.todo-input-form input {
    background-color: white;
    border: none;
    border-radius: 3px;
    outline :none;
    font-size: 18px;
    text-align: center;
    padding: 4px;
    width: 90%;
    box-shadow: 0 0 0 1px rgba(0,0,0,0.1),0 1px 1px rgba(0,0,0,0.1);
}

.todo-input-form button {
    padding: 0.5 rem;
    color: black;
    background-color: rgb(230, 237, 242);
    border: none;
    outline :none;
    width: 200px;

}

